<template>
  <div>
    <div class="tool-container" v-show="isShowToolBox">
      <span class="she-btn">设置</span>
      {{title}}
    </div>
    <app-main @updateRoute="updateRoute"></app-main>
    <nav-bar></nav-bar>
  </div>
</template>

<script>
  import { AppMain, NavBar } from './components'
  export default {
    name: 'llll',
    data() {
      return {
        isShowToolBox: true,
        title: ''
      }
    },
    components: {
      AppMain,
      NavBar
    },
    methods: {
      updateRoute() {
        if (this.$route.meta.isShowToolBox !== undefined && this.$route.meta.isShowToolBox === false) {
          this.isShowToolBox = false
        } else {
          this.isShowToolBox = true
          this.title = this.$route.meta.title
        }
      }
    },
    created() {
      this.updateRoute()
    }
  }
</script>

<style scoped>

</style>



