<template>
  <div class="title1-container">
    <span class="title1-main">{{title}}</span>
    <span class="title1-sub">{{subTitle}}</span>
  </div>
</template>
<script>
  export default {
    props: ['title', 'subTitle'],
    name: 'title1',
    data() {
      return {}
    }
  }
</script>

<style lang="scss">
  @function px2rem($px) {
    @return $px /75px * 1rem;
  }
  .title1-container{
    line-height: px2rem(60px);
    font-size: px2rem(24px);
    border-bottom: 1px solid #f0f0f0;
    padding-left: px2rem(30px);
  }
  .title1-main{
    color: #f6b951;
  }
  .title1-sub{
    color: #7f8699;
  }
</style>



