<template>
  <div class="title2-container">
    <span class="title2-main">{{title}}</span>
  </div>
</template>
<script>
  export default {
    props: ['title', 'subTitle'],
    name: 'title1',
    data() {
      return {}
    }
  }
</script>

<style lang="scss">
  @function px2rem($px) {
    @return $px /75px * 1rem;
  }
  .title2-container{
    line-height: px2rem(60px);
    font-size: px2rem(24px);
    padding-left: px2rem(30px);
  }
  .title2-main{
    color: #3f3f3f;
    padding-left:px2rem(18px);
  position:relative;
    &:before{
      content: '';
      width: px2rem(6px);
       height: px2rem(32px);
       background: #f3a117;
       position: absolute;
       left: 0;
       top: 50%;
      margin-top: px2rem(-16px);
      border-radius: 3px;
   }
  }
</style>



