<template>
  <div class="nav-container">
    <div class="nav-list">
      <div class="nav-item" v-for="item in navList">
        <router-link :to="{name: item.routerName}">
          <span class="nav-icon" :class="item.iconClass"></span>
          <div>{{item.navTitle}}</div>
        </router-link>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    name: 'navBar',
    data() {
      return {
        navList: [
          {
            iconClass: 'home',
            navTitle: '首页',
            routerName: 'home'
          },
          {
            iconClass: 'mytask',
            navTitle: '我的任务',
            routerName: 'mytask'
          },
          {
            iconClass: 'hall',
            navTitle: '任务大厅',
            routerName: 'hall'
          },
          {
            iconClass: 'personal',
            navTitle: '个人中心',
            routerName: 'personal'
          }
        ]
      }
    }
  }
</script>
<style>
</style>



