import Vue from 'vue'
import Router from 'vue-router'
const _import = require('./_import_' + process.env.NODE_ENV)
// in development-env not use lazy-loading, because lazy-loading too many pages will cause webpack hot update too slow. so only in production use lazy-loading;
// detail: https://panjiachen.github.io/vue-element-admin-site/#/lazy-loading

Vue.use(Router)

import Layout from '../views/layout/Layout'

/**
 * hidden: true                   if `hidden:true` will not show in the sidebar(default is false)
 * redirect: noredirect           if `redirect:noredirect` will no redirct in the breadcrumb
 * name:'router-name'             the name is used by <keep-alive> (must set!!!)
 * meta : {
    role: ['admin','editor']     will control the page role (you can set multiple roles)
    title: 'title'               the name show in submenu and breadcrumb (recommend set)
    icon: 'svg-name'             the icon show in the sidebar,
    noCache: true                if fasle ,the page will no be cached(default is false)
  }
 **/
export const constantRouterMap = [
  {
    path: '',
    component: Layout,
    redirect: '/',
    children: [
      { path: '/', name: 'home', component: _import('container/index'), hidden: true, meta: { isShowToolBox: false, title: '首页' }},
      { path: '/personal', name: 'personal', component: _import('container/personal'), hidden: true, meta: { title: '个人中心' }},
      { path: '/hall', name: 'hall', component: _import('container/hall'), hidden: true, meta: { title: '任务大厅' }},
      { path: '/classes', name: 'classes', component: _import('container/classes'), hidden: true, meta: { title: '分类' }},
      { path: '/mytask', name: 'mytask', component: _import('container/mytask'), hidden: true, meta: { title: '我的任务' }},
      { path: '/taskDetail/:tId', name: 'taskDetail', component: _import('container/taskDetail'), hidden: true, meta: { title: '任务详情' }}
    ]
  }
]

export default new Router({
  // mode: 'history', //后端支持可开
  scrollBehavior: () => ({ y: 0 }),
  routes: constantRouterMap
})

export const asyncRouterMap = [
  { path: '*', redirect: '/404', hidden: true }
]

